.productsWrapper {
    margin: 45px;
    /* display: flex; */
}

.productItem {
    width: 200px;
    height: 220px;
    background-color: #888a6e;
    margin-right: 20px;
    margin-bottom: 20px;
    float: left;
    cursor: pointer;
}

.itemTitle {
    text-align: center;
    top: 25%;
    position: relative;
}

.productImg {
    width: 100px;
    height: 100px;
    background-size: contain;
    background-repeat: no-repeat;
    position: relative;
    left: 50px;
    top: 20px;
    border: 1px solid #ddd;
    border-radius: 4px;
}